@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
*,*::after,*::before{margin:0;padding:0;box-sizing:border-box}body{background-color:var(--background);color:var(--font-color);font-family:"Poppins",sans-serif;transition:background 500ms;position:relative}body .bg_particle{position:absolute;left:0;bottom:0;width:100%;height:100%;z-index:-10;overflow:hidden}a{text-decoration:none;color:var(--font-color)}img{display:block}#root{width:100vw;height:100vh;display:flex;justify-content:center;align-items:flex-start;position:relative}#root::after{content:"N";left:0;top:0;position:absolute;font-size:20vw;opacity:.02;z-index:0}#root .main{width:100%;display:flex;flex-direction:column;justify-content:space-evenly;overflow:hidden;max-width:700px;z-index:1}#root .main .sidebar{flex:1 1}#root .main .container{flex:auto;display:flex;align-items:center;justify-content:center;padding:20px;background-color:var(--background)}@media(min-width: 415px){#root{align-items:center}#root .main{height:100%;max-height:500px;display:flex;flex-direction:row !important;justify-content:space-evenly}#root .main .container{border:5px solid var(--primary-color);border-top:5px solid var(--primary-color);border-left:none}}
.sidebar{background-color:var(--primary-color);background:linear-gradient(135deg, var(--primary-color), var(--secondary-color));display:flex;align-items:center;justify-content:space-evenly}.sidebar .sidebar__item{display:flex;align-items:center;justify-content:space-around;flex-direction:column;padding:10px;fill:#b3b3b3;color:#b3b3b3;position:relative}.sidebar .sidebar__item::after{content:"";width:100%;height:3px;background-color:var(--accent);position:absolute;bottom:0;transform:scaleX(0)}.sidebar .sidebar__item.active__item{fill:var(--active);color:var(--active)}.sidebar .sidebar__item.active__item::after{transition:transform 500ms;transform:scaleX(1)}@media(min-width: 415px){.sidebar{max-width:-webkit-max-content;max-width:-moz-max-content;max-width:max-content;height:100%;flex-direction:column}.sidebar .sidebar__item{width:100%}.sidebar .sidebar__item::after{content:"";width:3px;height:100%;background-color:var(--accent);position:absolute;right:0;transform:scaleY(0)}.sidebar .sidebar__item.active__item::after{transform:scaleX(1)}}
.about{line-height:2.5em;letter-spacing:1px}.about .hello_txt{background-color:var(--font-color);color:var(--font-color-inverse);padding:5px;position:relative}.about .hello_txt::after{content:"";position:absolute;width:10px;height:10px;left:0;bottom:0;transform:translateY(50%);border-radius:0 0 100%;background-color:var(--font-color)}.about .imp_primary{text-decoration:none;color:var(--secondary-color)}.about button{outline:none;background-color:var(--background);box-shadow:-5px 5px 0 0 var(--secondary-color);transition:box-shadow 300ms;border:1px solid var(--secondary-color);letter-spacing:2px;font-family:"Poppins"}.about button:active,.about button:hover{box-shadow:none}.about button a{padding:10px 20px;display:block;color:var(--font-color)}.about .secondary_btn{margin:0 10px}
.contact .link{display:flex;margin:10px 0;align-items:center}.contact .link span{height:100% !important;display:flex;align-items:center}.contact .link span.icon{padding:5px}.contact .link span.icon svg{width:20px;height:20px;fill:currentColor}
.project{width:100%;height:100%;display:flex;flex-direction:column;align-items:center;overflow-x:hidden;overflow-y:auto;scrollbar-width:none;-ms-overflow-style:none}.project::-webkit-scrollbar{width:0;height:0;background-color:transparent}.project .card_group{display:flex}.project .card_group .card{flex:1 1;margin:20px 5px;padding:2px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;transition:box-shadow 500ms;background-color:var(--bg-color);display:flex;flex-direction:column;border-radius:5px 5px 0 0}.project .card_group .card.info_card{height:auto}.project .card_group .card .box{display:flex}.project .card_group .card .box span{display:block;width:5px;height:5px;border-radius:5px;margin:5px;background-color:#ff7878}.project .card_group .card .box span:nth-child(2){background-color:#ff0}.project .card_group .card .box span:nth-child(3){background-color:green}.project .card_group .card .img{width:100%;height:auto;background-color:var(--primary-color)}.project .card_group .card .img img{width:100%;height:100%}.project .card_group .card .title{display:flex;align-items:center;justify-content:space-around}.project .card_group .card .title h5{letter-spacing:1.5px}.project .card_group .card .info{height:100%;padding:0 5px;background-color:var(--background);display:flex;align-items:center}.project .card_group .card .info p{padding:5px;font-size:15px}@media only screen and (max-width: 600px){.project .card_group{flex-direction:column;margin:10px 0}.project .card_group .card{margin:5px 0}}
.theme_toggle{position:fixed;right:10px;bottom:10px;display:flex;align-items:center;justify-content:center}.theme_toggle input.theme_checkbox{display:none}.theme_toggle .toggle{cursor:pointer;position:relative;width:50px;height:25px;border-radius:100px;background-color:var(--secondary-color);display:flex;align-items:center}.theme_toggle .toggle .theme_title{position:absolute;top:-20px;word-wrap:none;width:55px}.theme_toggle .toggle .toggle_handler{display:inline-block;position:relative;background-color:#ddf;z-index:1;width:25px;height:26px;border-radius:25px;transform:translateX(0%);transition:background-color,transform 300ms ease-out}.theme_toggle .toggle .theme_change{transform:translateX(100%)}.theme_toggle .dark{background-color:#666}.theme_toggle .dark .toggle_handler{background-color:#b3b3b3}
:root{--primary-color: #6200ea;--secondary-color: #651fff;--font-color: #311b92;--font-color-inverse: #ede7f6;--background: #fdfdff;--accent: #ff4081;--active: #ede7f6;--bg-color: #ddf}[data-theme=dark]{--font-color: #ead6ff;--font-color-inverse: #311b92;--background: #222;--bg-color: #111}
